            <p>2010년부터 2015년까지 약 5년간 온라인 쇼핑몰에서 웹디자이너로 근무한 경험이 있습니다. 디자인 경험을 더해 픽셀*, <span class="space_nowrap">여백(<span class="whitespace"></span>)</span> 등 <span class="space_nowrap"><span class="underline">디테일</span>의</span> 차이를 찾아내어 디자이너의 의도를 파악하고, 묻고 답하고 제안하는 커뮤니케이션을 통해 사용자에게 더 좋은 경험을 제공하고자 노력합니다.</p>
            <p>웹 에이전시 근무 경력과 마케팅 대행사 근무 경력으로 다양한 형태의 사이트 퍼블리싱 경험이 있습니다. 디자이너의 피드백을 환영하고 트렌드 탐색을 즐기며, 익숙함 속에 자연스레 스며드는 새로움을 좋아합니다.</p>
          </div>
        </div>

        <div class="about_cont exp">
          <strong class="about_cont_title">Experieance</strong>
          <div class="about_cont_text">
            <dl>
              <dt>2017.03 - 2022.03</dt>
              <dd>디트라이브 (마케팅 대행사)</dd>
              <dd>삼성 셰프컬렉션 패밀리허브, 삼성 스마트스쿨, 엘지 홈인 외 다수</dd>
