<template>
  <ul class="work_list">
    <!-- s: work item -->
    <li class="work_item" v-for="(item,i) in $store.state.projectdata" :key="i" ref="workItem" :data-url="item.url.toLowerCase()">

      <div class="work_item_wrap" >
        <button type="button" @click="methods.changeProjectPath(item.url)" class="work_item_lnk" :data-color="item.color">  

          <div class="work_item_label">
            <div class="work_item_flex">
              <span class="work_item_type">{{item.type}}</span>
              <span class="work_item_year">'{{item.year}}</span>
            </div>
          </div>

          <div class="work_item_thumb">
            <div class="work_item_img">
              <img :src="item.thumb" alt="">
            </div>
          </div>

          <div class="work_item_text">
            <div class="work_item_title">
              <strong>{{item.title}}</strong>
            </div>
            <p class="work_item_case">{{item.case}}</p>
          </div>

        </button>
      </div>

    </li>
    <!-- e: work item -->
  </ul>
</template>

<script>
import { useRouter, onBeforeRouteUpdate } from 'vue-router'

export default {
  name: 'MainWorkList',
  setup(){
    const router = useRouter();
    const methods = {
      changeProjectPath: (url)=>{
        router.push({path: '/' + url});
      }
    };

    onBeforeRouteUpdate((to, from, next) => {
      next();
    });

    return{
      methods
    }
  },
}
</script>