<template>
  <div class="work_preview">
    
    <!-- play images -->
    <div class="work_images">
      <div class="work_images_item" v-for="(list,i) in data.workimgs" :key="i">
        <div class="work_images_box">
          <img :src="item.image" :alt="item.alt" v-for="(item,idx) in list" :key="idx">
        </div>
      </div>
    </div>

    <!-- work heading -->
    <div class="work_heading">
      <div class="work_title">
        <h2 class="line_wrap"><span class="">WORK</span></h2>
        <p class="work_desc">
          웹 에이전시 근무 경력과 마케팅 대행사 근무 경력으로 <br>
          비즈니스 웹 사이트, 쇼핑몰, 프로모션 사이트 등
          다양한 형태의 퍼블리싱 작업을 진행했습니다.<br>
          다음은 몇가지 유형의 프로젝트 입니다.<br>
        </p>
      </div>
    </div>

  </div>
</template>

<script>
import { onMounted, onBeforeUnmount } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import workimgs from '../data/workpreviewimgs.js'

export default {
  name: 'MainWorkHeading',
  setup(){
    const data = {
      workimgs: workimgs
    };
    const methods = {
      workPreview : ()=>{
        const workPic = document.querySelectorAll('.work_images_box');
        workPic.forEach((pic) =>{

          pic.appendChild(pic.querySelector('img').cloneNode(true));
          
          const images = Array.from(pic.querySelectorAll('img'));
          const firstOutList = images.slice(1);
          const duration = 0.75;
          const pause = 1.5;
          const stagger = duration + pause;

          gsap.to('.work_images_item',{
            scrollTrigger: {
              trigger: '.work_preview',
              start: 'top center',
              duration: 1.2,
            },
            stagger: 0.2,
            ease: 'none',
            clipPath: 'polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)'
          });
          
          const tl = gsap.timeline({
            scrollTrigger: {
              trigger: '.work_preview',
              start: 'top center',
              end: 'bottom top',
            },
            repeat: -1,
          });

          tl.from(firstOutList,{
            '--clip-scale-x1': '0%',
            '--clip-scale-x2': '-40%',
            duration: duration,
            stagger: {
              each: stagger,
            },
            ease: 'power2.out',
          },0).to(firstOutList,{
            '--clip-scale-x1': '100%',
            '--clip-scale-x2': '100%',
            duration: duration,
            stagger: {
              each: stagger,
            },
            ease: 'power2.out',
          },pause);

        });
      },
    }

    onMounted(()=>{
      methods.workPreview();
    });
    onBeforeUnmount(()=>{
      ScrollTrigger.getAll().forEach((t) => t.kill());
    });

    return{
      data,
      methods
    }
  }
}
</script>