<template>
    <div class="work_content" id="work_content" data-scroll>
      
      <!-- s: sticky text -->
      <div class="work_content_info" data-scroll data-scroll-sticky data-scroll-target="#work_content">
        <div class="marquee_wrap">
          <div class="marquee_text">
            <div class="text">
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
            </div>
          </div>
        </div>
      </div>
      <!-- e: sticky text -->

      <!-- s: work list -->
      <main-work-list></main-work-list>
      <!-- e: work list -->

    </div>
</template>

<script>
import { onMounted, onBeforeUnmount } from 'vue'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import MainWorkList from './MainWorkList.vue'

export default {
  name: 'MainWorkContent',
  components: { 
    MainWorkList 
  },
  setup(){
    const methods = {
      listOffScreen: ()=>{
        ScrollTrigger.create({
          trigger: '.work_content',
          start: 'top bottom',
          end: 'bottom top',
          toggleClass: 'ani',
          invalidateOnRefresh: true,
        });
      },
    }

    onMounted(()=>{
      methods.listOffScreen();
    });
    onBeforeUnmount(()=>{
      ScrollTrigger.getAll().forEach((t) => t.kill());
    });

    return{
      methods
    }
  }
}
</script>